import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import { styles } from "./styles";
import clsx from "clsx";

const Stops = props => {
    const classes = props.classes;
    const qtd = parseInt(props.qtd);
    const setClass =
    qtd === 0 ? classes.noStop : qtd === 1 ? classes.oneStop : classes.moreStop;
    return <span className={clsx(setClass, props.className)} />;
};

Stops.propTypes = {
    classes: PropTypes.object.isRequired,
    qtd: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired,
    className: PropTypes.string,
};

export default withStyles(styles, { withTheme: true })(Stops);
